@mixin make-cta-07() {
    display: block;
    position: relative;
    overflow: hidden;
    width: 100%;

    &__bg-items {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;

        &::before {
            content: "";
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
        }
    }

    &__ft-items {
        position: relative;
        display: flex;
        align-items: center;
        padding: 10%;

        @media all and ($viewport-type: $tabletl) {
            padding: 8% 7%;
        }

        @media all and ($viewport-type: $tabletm) {
            padding: 9% 7%;
        }

        @media all and ($viewport-type: $tablets) {
            padding: 10% 6%;
        }

        @media all and ($viewport-type: $mobile) {
            padding: 10% 8%;
        }

        &__left-items {
            width: calc(50% - #{$measure} * 15);
            margin-right: $measure * 15;

            @media all and ($viewport-type: $tabletm) {
                width: calc(50% - #{$measure} * 10);
                margin-right: $measure * 10;
            }
        }

        &__right-items {
            width: 50%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
}

@mixin make-cta-07-modifier(
    $background-position: false,
    $background-size: false,
    $border-radius: false,
    $breakpoint: false,
    $hover-background-img-zoom: false,
    $hover-shadow: false,
    $hover-title-color: false,
    $hover-transform: false,
    $mask-color: false,
    $text-align: false,
    $title-color: false,
    $title-font: false,
    $title-font-weight: false,
    $title-max-lines: false
) {
    @if $background-position {
        &__bg-items {
            background-position: $background-position;
        }
    }

    @if $background-size {
        &__bg-items {
            background-size: $background-size;
        }
    }
    @if $breakpoint {
        @media all and ($viewport-type: $breakpoint) {
            &__ft-items {
                display: block;

                &__left-items,
                &__right-items {
                    width: 100%;
                }

                &__right-items {
                    margin-top: $measure * 4;
                    display: block;
                }
            }
        }
    }

    @if $mask-color {
        &__bg-items {
            &::before {
                background: $mask-color;
            }
        }
    }

    @if $hover-shadow and $hover-transform {
        transition: box-shadow $time-b $ease-standard-a, transform $time-b $ease-standard-a;
        @media all and (hover: hover) {
            &:hover,
            &:focus,
            &:active {
                box-shadow: $hover-shadow;
                transform: $hover-transform;
            }
        }
    } @else if $hover-shadow {
        transition: box-shadow $time-b $ease-standard-a;
        @media all and (hover: hover) {
            &:hover,
            &:focus,
            &:active {
                box-shadow: $hover-shadow;
            }
        }
    } @else if $hover-transform {
        transition: transform $time-b $ease-standard-a;
        @media all and (hover: hover) {
            &:hover,
            &:focus,
            &:active {
                transform: $hover-transform;
            }
        }
    }

    @if $hover-background-img-zoom {
        @media all and (hover: hover) {
            &:hover,
            &:focus,
            &:active {
                .g--cta-07 {
                    &__bg-items {
                        transform: scale($hover-background-img-zoom);
                    }
                }
            }
        }
    }

    @if $hover-title-color {
        @media all and (hover: hover) {
            &:hover,
            &:focus,
            &:active {
                .g--cta-07 {
                    &__ft-items {
                        &__left-items {
                            &__title {
                                color: $hover-title-color;
                            }
                        }
                    }
                }
            }
        }
    }

    @if $border-radius {
        border-radius: $border-radius;
    }

    @if $text-align == "center" {
        .g--cta-07 {
            &__ft-items {
                padding-left: $measure * 15;
                padding-right: $measure * 15;
                text-align: center;

                @media all and ($viewport-type: $tabletl) {
                    padding-left: $measure * 10;
                    padding-right: $measure * 10;
                }

                @media all and ($viewport-type: $tablets) {
                    padding-left: $measure * 5;
                    padding-right: $measure * 5;
                }
            }
        }
    }

    @if $text-align == "left" {
        .g--cta-07 {
            &__ft-items {
                padding-left: $measure * 10;
                padding-right: $measure * 30;
                text-align: left;

                @media all and ($viewport-type: $tabletl) {
                    padding-left: $measure * 5;
                    padding-right: $measure * 25;
                }

                @media all and ($viewport-type: $tabletm) {
                    padding-right: $measure * 20;
                }

                @media all and ($viewport-type: $tablets) {
                    padding-left: $measure * 5;
                    padding-right: $measure * 5;
                }
            }
        }
    }

    @if $text-align == "right" {
        .g--cta-07 {
            &__ft-items {
                padding-left: $measure * 30;
                padding-right: $measure * 10;
                text-align: right;

                @media all and ($viewport-type: $tabletl) {
                    padding-left: $measure * 25;
                    padding-right: $measure * 5;
                }

                @media all and ($viewport-type: $tabletm) {
                    padding-left: $measure * 20;
                }

                @media all and ($viewport-type: $tablets) {
                    padding-left: $measure * 5;
                    padding-right: $measure * 5;
                }
            }
        }
    }

    .g--cta-07 {
        &__bg-items {
            @if $hover-background-img-zoom {
                transition: transform $time-b $ease-standard-a;
            }
        }

        &__ft-items {
            &__left-items {
                &__title {
                    @if $title-color {
                        color: $title-color;
                    }

                    @if $title-font {
                        @extend .f--font-#{$title-font} !optional;
                    }

                    @if $title-font-weight {
                        font-weight: $title-font-weight;
                    }

                    @if $title-max-lines {
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: $title-max-lines;
                        -webkit-box-orient: vertical;
                    }

                    @if $hover-title-color {
                        transition: color $time-b $ease-standard-a;
                    }
                }
            }
        }
    }
}
